/*#region content*/

.content-container {
    display: flex;
    justify-content: center;
    width: 100%;
}

.content {
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    height: 100%;
    width: 60%;
    padding: 1%;
    padding-top:3%;
}
/*#endregion */

/*#region item*/
.item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: auto;
    width: 80%;
    margin-bottom: 3%;
    border-radius: 15px;
    margin-top:2%;
    background: white;
    transition: 2s;
}

.item:hover {
    box-shadow:4px 4px 20px 4px lightgray !important;
    transition: 1s;
    background:#F0F0F0;
}

.mock-box {
    height: 8vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-top: 0%;
    
}

.method {
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:1.5%;

    font-size: 2.5vw;
    font-weight: 100;
}

.path {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:1.5%;
    font-size: 1.4vw;
    font-weight: 200;
}

.path img {
    height: 1.2vw;
    padding-left: 1.2%;
    transition: 0.6s;
}

.path img:hover{
    cursor: pointer;
    height: 1.35vw;
    transition: 0.6s;
}

.status {
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:1.5%;

    font-size: 3.1vw;
    font-weight: 100;
}

.trash {
    width: 10%;
    display: flex;
    justify-content: center;
    align-items: top;

}

.trash img{
    height: 1.8vw;
    transition: 0.6s;
    padding-left:20%;
    padding-top:15%;
    
}

.trash img:hover{
    cursor: pointer;
    height: 1.9vw;
    transition: 0.6s;
    padding-top:12%;
}

.id {
    font-size: 0.5vw;
    font-weight: 200;
    padding-top: 0.2%;
    padding-bottom: 0.2%;
}

/*#endregion */

/*#region mock-create*/

.mock-create {
    box-shadow: 3px 3px 3px 3px gray;
    transition: 1s;
}

.mock-create:hover {
    background:lightgray;
}

.mock-create img {
    height: 2.8vw;
    filter: invert(52%) sepia(0%) saturate(2026%) hue-rotate(144deg) brightness(97%) contrast(87%);
}

.mock-create:hover img {
    filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(71deg) brightness(104%) contrast(104%);
    cursor: pointer;
    transition: 1s;
}

.mock-create:hover img:hover {
    height: 3.5vw;
    transition: 1s;
}

.mock-create:hover label {
    color: white;
    transition: 0.5s;
}

.mock-create:hover input, .mock-create:hover textarea {
    background: white;
    transition: 0.5s;
}

/*#endregion */

/*#region mock-create-field*/

.mock-create-field {
    width: 100%;
    height:100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
    padding-top: 3%;
    padding-left: 1%;
    padding-right: 1%;
    padding-bottom: 0%;
}

.mock-create-field div {
    margin-bottom: 1%;
    display: flex;
    flex-direction: column;
    padding-right: 2%;
}

.mock-create-field label {
    color: gray;
    font-size: 1.3em;
    font-weight: 300;
    padding-left: 2%;
}

.mock-create-field input, .mock-create-field textarea {
    border-radius: 15px;
    text-align: left;
    border:none;
    height: 5vh;
    margin-left: 1%;
    background:#F5F5F5;
    width: 100%;
    padding-left: 2%;
    padding-right: 2%;

    font-size: 1em;
    font-weight: 200;
}

.mock-create-field input:focus, .mock-create-field textarea {
    outline: none;
}

.mock-create-field textarea {
    height: 50vh;
    padding-top: 2%;
    padding-left: 3%;
    padding-right: 3%;
}

/*#endregion */

/*#region field-button*/

.field-button {
    display:flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 6vh;
    flex-direction: row !important;
}

.field-button button {
    border-radius: 15px;
    border:none;
    height: 80%;
    width: 20%;
    margin-right: 10%;
    margin-left: 10%;
    transition: 1s;
}

.create-button {
    color: white;
    box-shadow: 1px 1px 3px 1px white;
    transition: 1s;
}

.cancel-button {
    color: white;
    box-shadow: 1px 1px 3px 1px white;
    transition: 1s;
}

.field-button button:hover, .field-button button:hover label {
    cursor: pointer;
}

.create-button label, .cancel-button label {
    color: white;
} 

.create-button:hover {
    color: green;
    box-shadow: 2px 2px 2px 2px green;
    transition: 0.5s;
}

.create-button:hover label {
    color: green !important;
    font-size: 1.5em;
    transition: 0.5s;
    font-weight: 400;
}

.cancel-button:hover {
    box-shadow: 2px 2px 2px 2px red;
    transition: 0.5s;
}

.cancel-button:hover label {
    color: red !important;
    font-size: 1.5em;
    transition: 0.5s;
    font-weight: 400;
}

.mock-create:hover button {
    background: white;
    transition: 0.5s;
}

.mock-create:hover button label {
    color: lightgray;
    transition: 0.5s;
}

/*#endregion */