.header{
    height: 12vh;
    width: 100%;
}

.logo {
    height:60%;
    background:black;
    display: flex;
    justify-content: space-between;
}

.logo-block {
    display: flex;
    width: 18%;
}

.logo-next {
    background: black;
    border: 0px solid lime;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40%;
    
}

.logo-next label {
    color: lime;
    font-size: 2em;
    font-weight: 100;
}

.logo-mock {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 3%;
    width: 30%;
    background: lime;
    border: 5px solid black;
}

.logo-mock label {
    color: black;
    font-size: 1.2em;
    font-weight: 900;
}

.ambient {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10%;
    background: black;
    border: 0px solid lime;
}

.ambient label {
    color: lime;
    font-size: 1.2em;
    font-weight: 300;
}

.pbi {
    height:40%;
    background: green;
}
